.link-book{
    text-decoration: none;
    .bookcard-box{
        position: relative;
        width: 300px;
        margin-bottom: 30px;
        @media screen and (max-width: 768px){
        width: 200px;
        }

        .cover-book{
            width: 100%;
        }

        .div-discribe{

            .title-publish{
                margin: 0;
                margin-top: 10px;
                color: #9ba6a5;
                font-weight: 200;
            }

            .title-book{
                color: #fff;
                margin: 0;
                margin-top: 10px;
                text-transform: uppercase;
            }
        }

        .cirle-rate{
            position: absolute;
            background-color: #000;
            top: 10px;
            right: 10px;
            border: 2px solid #000;
            width: 50px;
            height: 50px;
            border-radius: 50px;

            .principale-borde{
                width: 46px;
                height: 46px;
                border-radius: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                
                .rate{
                    color:#fff;
                }
            }
        }
    }
}

.green{
    border:2px solid #42b883;
}
.orange{
 border:2px solid #f1b963;
}
.red{
border:2px solid red;
}